.contacts{
  padding: 3.125em 0 5em;
  &__grid{
    margin-top: 1.875em;
    row-gap: 1.25em;
  }
}

.contacts-group{
  padding: 1.25em;
  border-bottom: 1px solid rgba($darkBlue, 0.20);
  &__items{
    @include grid100gap(.625em)
  }
  .socials{
    align-items: flex-start;
    flex-direction: column;
    &__item{
      @include lastM(0 0 .25em);
      span{
        font-size: 1.125em;
      }
      svg{
        margin-right: .625em;
      }
    }
  }
}


.contacts-item{
  display: grid;
  grid-template-columns: 15.75em calc(100% - 15.75em);
  &__title,
  &__desc{
    font-size: 1.125em;
  }
  &__desc{
    font-weight: 300;
  }
}

.map{
  height: 32.5625em;
  .ymaps-2-1-79-map {
    -webkit-filter: grayscale(100%);
  }
}

.contacts-form{
  width: 46.875em;
  max-width: 100%;
  margin: 5em auto 0;
  &__fields{
    margin-top: 1.875em;
    @include grid100gap(.9375em);
  }
  .fg{
    input,
    textarea{
      font-size: .875em;
      padding: 0 1.07em;
      height: 3.14em;
    }
    textarea{
      padding: 1.07em;
      height: 8.58em;
    }
  }
  &__nav{
    margin-top: .9375em;
    display: flex;
    justify-content: flex-end;
  }
  &__btn{
    padding: 0 1.75em;
    height: 2.5625em;
    border-radius: .3125em;
    span{
      font-size: 1em;
    }
  }
  .m-check{
    margin-top: .3125em;
  }
}


@media screen and (max-width: 992px){
  .contacts{
    margin-top: -.625em;
    padding: 0 0 2.5em;
    &__grid{
      margin-top: .3125em;
      gap: 0;
      grid-template-columns: 100%;
    }
  }

  .contacts-group{
    padding: 1.25em .9375em;
    &--order1{
      order: -1;
    }
    &__items{
      gap: .875em;
    }
  }

  .map{
    height: 24.375em;
  }

  .contacts-form{
    margin-top: 2.5em;
    &__fields{
      margin-top: .9375em;
      gap: .625em;
    }
    &__btn{
      width: 100%;
    }
  }
}


@media screen and (max-width: 576px){
  .contacts-group{
    .socials{
      &__item{
        span{
          font-size: 1em;
        }
      }
    }
  }

  .contacts-item{
    grid-template-columns: 100%;
    gap: .25em;
    &__title,
    &__desc{
      font-size: 1em;
      line-height: 130%;
    }
  }

  .contacts-form{
    .m-check{
      label{
        padding-left: 1.875em;
        &:before{
          left: .3125em;
          top: 50%;
          transform: translateY(-50%);
        }
      }
      &__title{
        font-size: .75em;
      }
    }
  }
}
