@import "../vars";
.m-btn {
    display: flex;
    align-items: center;
    justify-content: center;
    transition: all ease-out .225s;
    padding: 0 1em;
    height: 2.5em;
    &:focus {
        box-shadow: none;
    }
}

.m-btn-dark{
    background-color: $darkBlue;
    border: 1px solid $darkBlue;
    color: #fff;
    &:hover{
        background-color: transparent;
        color: $darkBlue;
        ._fill{
            fill: $darkBlue;
            stroke: $darkBlue;
        }
        ._stroke{
            stroke: #fff;
        }
    }
}

.m-btn-dark-outline{
    border: 1px solid $darkBlue;
    color: $darkBlue;
    &:hover{
        background-color: $darkBlue;
        color: #fff;
    }
}

.m-btn-transparent{
    border: 1px solid transparent;
    color: $darkBlue;
    &:hover{
        border-color: $darkBlue;
        color: $darkBlue;
    }
}


.m-btn-light{
    background-color: $lightBlue;
    color: $darkBlue;
    border: 1px solid $lightBlue;
    &:hover{
        background-color: transparent;
        color: $lightBlue;
    }
}
