.modal {
    position: fixed;
    left: 0;
    top: 0;
    width: 100%;
    height: calc(100 * var(--vh));
    overflow-y: auto;
    z-index: 80;
    opacity: 0;
    pointer-events: none;
    //transition: all .3s;
    padding: 1em 0;
    text-align: center;

    &:after {
        content: '';
        display: inline-block;
        height: 100%;
        vertical-align: middle;
        margin-right: -4px;
    }

    &__dialog {
        text-align: left;
        vertical-align: middle;
    }

    &__bg {
        background: rgba(0, 0, 0, 0.40);
        position: fixed;
        width: 100%;
        height: calc(100 * var(--vh));
        top: 0;
        left: 0;
        display: none;
        z-index: 2;
    }

    &._active {
        opacity: 1;
        pointer-events: unset;

        .modal__bg {
            display: block;
        }
    }

    &__dialog {
        background: $lightGrey;
        width: 31.25em;
        max-width: calc(100% - 2em);
        position: relative;
        z-index: 3;
        padding: 2.5em;
        border-radius: .9375em;
        display: inline-block;
    }
    &__top{
        display: flex;
        align-items: center;
        justify-content: space-between;
        margin-bottom: 1.875em;
    }
    .close-btn {
        width: 1.875em;
        height: 1.875em;
        background-color: transparent;
        @include center;
        z-index: 3;
        svg {
            width: 100%;
            height: auto;
        }
        &:hover {
            path {
                stroke: $darkBlue;
            }
        }
    }
}

.modal-form{
    @include grid100gap(.9375em);
    &__btn,
    &__btn2{
        margin-top: .3125em;
        border-radius: .3125em;
        width: 100%;
        height: 2.6875em;
        span{
            font-size: 1.125em;
        }
    }
    &__btn2{
        margin-top: -.3125em;
    }
}


@media screen and (max-width: 992px){
    .modal{
        &:after{
            display: none;
        }
    }
}


@media screen and (max-width: 576px){
    .modal{
        padding: 0;
        &__dialog{
            max-width: 100%;
            border-radius: 0;
            padding: 0;
            height: $vh;
        }
        &__top{
            margin: 0;
            justify-content: center;
            padding: 0 .9375em;
            height: 4em;
            position: relative;
        }
        &__bottom{
            height: calc(100 * var(--vh) - 4em);
            overflow-y: auto;
            padding: 3.125em .9375em;
        }
        .close-btn{
            position: absolute;
            left: .9375em;
            padding: .9375em;
            width: 2.375em;
            height: 2.375em;
            background-image: url(../img/arrow-left.svg);
            @include bgImgSize(.5em);
            svg{
                display: none;
            }
        }
    }

    .modal-form{
        &__btn{
            margin-top: .9375em;
        }
        &__btn2{
            margin-top: -.3125em;
        }
    }
}
