.profile{
  padding: 3.125em 0 5em;
  &__inner{
    display: flex;
    justify-content: center;
    margin-top: 1.875em;
    position: relative;
  }
  &__links{
    position: absolute;
    left: 0;
    top: 1.75em;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
  }
  &__link{
    font-size: 1.125em;
    line-height: 130%;
    text-decoration: underline;
    @include lastM(0 0 .5em);
    &:hover{
      text-decoration: none;
    }
  }
}

.user{
  display: flex;
  flex-direction: column;
  text-align: center;
  &__img{
    width: 10.25em;
    position: relative;
    margin: 0 auto;
    &:not(:hover){
      .img-input{
        opacity: 0;
      }
    }
  }
  &__name{
    font-weight: 500;
    min-height: 2.3125em;
    @include center;
    margin-top: 1.875em;
  }
  &__email{
    font-weight: 300;
    min-height: 2.1875em;
    @include center;
  }
}

.img-input{
  transition: all .3s;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  @include center;
  background: linear-gradient(0deg, rgba(2, 21, 62, 0.70) 0%, rgba(2, 21, 62, 0.70) 100%);
  border-radius: 50%;
  img{
    width: 2.25em;
  }
}


@media screen and (max-width: 992px){
  .profile{
    padding: 0 0 3.75em;
    margin-top: -.625em;
    &__inner{
      margin-top: 1.5625em;
    }
    &__link{
      font-size: 1em;
    }
  }
}


@media screen and (max-width: 576px){
  .profile{
    &__inner{
      flex-direction: column;
      align-items: center;
    }
    &__links{
      margin-right: auto;
      position: static;
      padding: 4.75em .9375em 0;
    }
  }

  .user{
    &__name{
      min-height: unset;
      margin-top: 1em;
    }
    &__email{
      min-height: unset;
      margin-top: .5em;
    }
  }
}
