.fg {
  position: relative;
  margin: 0;
  textarea,
  input {
    border: 1px solid $darkBlue;
    padding: 0 .83em;
    height: 2.39em;
    width: 100%;
    font-size: 1.125em;
    background-color: transparent;
    color: $darkBlue;
    border-radius: .28em;
    &::placeholder {
      font-weight: 300;
      color: rgba($darkBlue,.5);
    }
  }
  textarea{
    resize: vertical;
    padding: .83em;
    height: 4.4375em;
  }
}

.img-input,
.filter-types__item,
.m-check{
  position: relative;
  input{
    position: absolute;
    opacity: 0;
    width: 100%;
    height: 100%;
    left: 0;
    top: 0;
    z-index: 2;
    cursor: pointer;
  }
}

.m-check{
  label{
    position: relative;
    cursor: pointer;
    display: block;
    padding-left: 2.3125em;
    min-height: 1.5625em;
    padding-top: .21875em;
    &:before,
    &:after{
      pointer-events: none;
    }
    &:before{
      content: '';
      position: absolute;
      z-index: 1;
      left: 0;
      top: 0;
      border: 1px solid $darkBlue;
      width: 1.5625em;
      height: 1.5625em;
      @include bgImgSize(0);
      transition: all .0s;
    }
    a{
      text-decoration: underline;
      position: relative;
      z-index: 2;
      &:hover{
        text-decoration-color: transparent;
        color: inherit;
      }
    }
  }
  &__title{
    font-size: 1.125em;
    display: block;
  }
}

.m-check {
  input {
    &:checked+label {
      &::before {
        background-color: $darkBlue;
        background-size: .8125em auto;
      }
    }
  }
  label {
    &:before {
      border-radius: 0.3125em;
      background-image: url(../img/check.svg);
    }
  }
  &--small{
    input {
      &:checked+label {
        &::before {
          background-size: .625em auto;
        }
      }
    }
    label{
      display: flex;
      align-items: center;
      min-height: .9375em;
      padding-top: 0;
      padding-left: 1.5625em;
      font-weight: 300;
      &:before{
        border-radius: 0.1875em;
        width: .9375em;
        height: .9375em;
      }
    }
    .m-check__title{
      font-size: .875em;
    }
  }
}


