.catalog{
  &__content{
    grid-column: 2/-1;
  }
}

.filter-search{
  &__icon{
    left: unset;
    right: .94em;
  }
  input{
    padding: 0 2.69em 0 .94em;
    height: 3.125em;
  }
}

.filter-btn{
  display: none;
}

.filter{
  &__items{
    @include grid100gap(1.875em);
  }
  &__checks{
    @include grid100gap(.625em);
  }
}

.filter-weight{
  &__grid{
    display: grid;
    grid-template-columns: calc(50% - 2.8125em / 2) 2.8125em calc(50% - 2.8125em / 2);
    margin-top: .9375em;
  }
  .fg{
    input{
      padding-left: 2.75em;
      font-size: 1em;
      border-radius: 0.3125em;
      height: 2.875em;
    }
  }
  &__placeholder{
    position: absolute;
    left: .9375em;
    top: 50%;
    transform: translateY(-50%);
    font-size: 1em;
    font-weight: 300;
    color: $darkBlue50;
  }
  &__split{
    @include center;
    img{
      width: 1.125em;
    }
  }
}

.product-card{
  border-radius: 0.625em;
  background-color: #fff;
  border: 1px solid #fff;
  box-shadow: 0 0 .9375em rgba(2, 21, 62, 0.15);
  display: flex;
  flex-direction: column;
  position: relative;
  transition: all .3s;
  &:hover{
    color: $darkBlue;
    border-color: $darkBlue;
  }
  &__img{
    @include imgCover(100%);
  }
  &__content{
    padding: 0 1.875em 1.875em;
    flex-grow: 1;
    display: flex;
    flex-direction: column;
  }
  &__title{
    min-height: 3.6em;
    @include truncate(3);
  }
  &__bottom{
    margin-top: auto;
    padding-top: 1.875em;
    @include grid100gap(.375em);
  }
  &__formula{
    font-size: 1.125em;
    color: rgba($darkBlue, 0.20);
  }
  &__weight{
    font-weight: 500;
    font-size: 1em;
  }
  &__badges{
    position: absolute;
    top: .625em;
    right: .625em;
    z-index: 3;
    .badge{
      margin-bottom: .625em;
    }
  }
}

.badge{
  width: 3.125em;
  height: 3.125em;
  @include center;
  border-radius: 50%;
  border: 1px solid $darkBlue;
  font-weight: 500;
  &._solid{
    background-color: $darkBlue;
    color: #fff;
  }
  &__title{
    font-size: .9375em;
  }
}

.catalog-desc{
  padding: 5em 0;
  background-color: $lightGrey;
  .text{
    grid-column: 2/-1;
  }
}

.text{
  font-weight: 300;
  p{
    font-size: 1.375em;
    line-height: 120%;
    @include lastM(0 0 .5em);
  }
}

.filter-types{
  &__items{
    display: flex;
    flex-wrap: wrap;
    margin-top: .625em;
    width: calc(100% + .625em);
  }
  &__item{
    margin: .625em .625em 0 0;
    label{
      padding: 0 1.25em;
      height: 2.25em;
      border-radius: 0.3125em;
      background-color: $lightGrey;
      @include center;
      font-weight: 300;
      font-size: 1em;
      &:after{
        content: '';
        flex-shrink: 0;
        background-image: url(../img/clear.svg);
        @include bgImgSize(100%);
        height: .75em;
        width: 0;
      }
    }
    input:checked~label{
      color: #fff;
      background-color: $darkBlue;
      &:after{
        margin-left: .625em;
        margin-right: -.5em;
        width: .75em;
      }
    }
  }
}


@media screen and (max-width: 992px){
  ._filter-opened{
    .filter{
      opacity: 1;
      pointer-events: unset;
    }
  }

  .catalog{
    &__grid{
      display: block;
    }
  }

  .filter-btn{
    margin-bottom: 1.25em;
    display: flex;
    border-radius: 0.3125em;
    width: 100%;
    span{
      font-size: 1.125em;
    }
    img{
      margin-right: .625em;
      width: 1.1875em;
    }
  }

  .filter{
    position: fixed;
    background-color: #fff;
    z-index: 16;
    left: 0;
    top: 0;
    width: 100%;
    height: $vh;
    opacity: 0;
    pointer-events: none;
    transition: all .3s;
    &__scroll{
      padding: 1.25em .9375em 3em;
      height: calc(100 * var(--vh) - 4em);
      overflow-y: auto;
    }
    .catalog-menu__top{
      background-color: $lightGrey;
    }
  }

  .filter-search{
    input{
      height: 2.75em;
    }
  }

  .product-card{
    &__content{
      padding: 0 .9375em .9375em;
    }
    &__bottom{
      padding-top: .25em;
      gap: .125em;
    }
    &__badges{
      top: .3125em;
      right: .3125em;
      .badge{
        margin-bottom: .3125em;
      }
    }
  }

  .badge{
    width: 1.6875em;
    height: 1.6875em;
    @media screen and (min-width: 577px){
      font-size: 1.375em;
    }
    &__title{
      font-size: .45em;
    }
  }

  .catalog-desc{
    display: none;
  }
}


@media screen and (max-width: 576px){
  .product-card{
    border-radius: .3125em;
    &__title{
      font-size: .875em;
    }
    &__formula{
      font-size: .75em;
    }
    &__weight{
      font-size: .625em;
    }
  }

  .text{
    p{
      font-size: .875em;
    }
  }
}


