.footer{
  background-color: $lightBlue;
  &__logo{
    display: block;
    padding: 1.66em 0;
    width: 14.375em;
    max-width: 100%;
    img{
      width: 100%;
      display: block;
    }
  }
  &__title{
    margin-bottom: 1.56em;
  }
  &__links{
    display: flex;
    flex-direction: column;
    align-items: flex-start;
  }
  &-contacts__item a,
  &__link{
    border-top: 1px solid transparent;
    border-bottom: 1px solid transparent;
    &:hover{
      color: $darkBlue;
      border-bottom-color: $darkBlue;
    }
  }
  &__c{
    color: $darkBlue50;
  }
}

.footer-top{
  padding: 5em 0;
}

.footer-contacts__item,
.footer__link{
  font-weight: 300;
  font-size: 1em;
  @include lastM(0 0 .625em);
}

.footer-contacts{
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  max-width: 14em;
}

.footer-subscribe{
  &__grid{
    position: relative;
  }
  .fg{
    input{
      height: 3.28em;
      font-size: .875em;
      padding: 0 4.28em 0 1.07em;
      border-radius: .14em;
    }
  }
  &__btn{
    position: absolute;
    top: 0;
    right: 0;
    width: 2.875em;
    height: 2.875em;
    @include center;
    border-radius: .125em;
    svg{
      width: 1.5em;
    }
    &:not(:hover){
      ._outline{
        stroke: $lightBlue;
        fill: none;
      }
    }
  }
  &__hint{
    margin-top: .56em;
  }
}

.footer-socials{
  margin-top: 1.5625em;
}

.socials{
  display: flex;
  align-items: center;
  &__item{
    display: flex;
    align-items: center;
    @include lastM(0 .6875em 0 0);
    svg{
      width: 1.5em;
      margin-right: .3125em;
    }
    span{
      font-size: .875em;
      border-top: 1px solid transparent;
      border-bottom: 1px solid transparent;
      transition: all .3s;
    }
    &:hover{
      color: $darkBlue;
      span{
        border-bottom-color: $darkBlue;
      }
    }
  }
}

.footer-bottom{
  padding: 1.875em 0;
  border-top: 1px solid $darkBlue50;
  text-align: center;
}


@media screen and (max-width: 992px){
  .footer{
    &__logo{
      padding: 0;
      width: 10em;
    }
    &__title{
      margin-bottom: .9375em;
    }
  }

  .footer-top{
    padding: 1.875em 0 3.125em;
    &__inner{
      row-gap: 3.125em;
    }
    ._col1,
    ._col4{
      order: -1;
    }
  }

  .footer-subscribe{
    margin-top: 1.25em;
  }

  .socials{
    flex-direction: column;
    align-items: flex-start;
    &__item{
      @include lastM(0 0 .625em);
    }
  }

  .footer-socials{
    margin-top: 1.375em;
  }

  .footer-bottom{
    text-align: left;
  }
}


@media screen and (max-width: 576px){
  .footer{
    &__logo{
      width: 6.25em;
    }
  }

  .footer-top{
    ._col1,
    ._col4{
      grid-column: 1/-1;
    }
  }

  .footer-contacts__item,
  .footer__link{
    font-size: .875em;
    @include lastM(0 0 .71em);
  }

  .footer-contacts{
    max-width: 10em;
  }

  .footer-subscribe{
    .fg{
      input{
        height: 2.85em;
      }
    }
    &__btn{
      width: 3.125em;
      height: 2.5em;
      svg{
        width: 1.25em;
      }
    }
  }
}
