.blog-index{
  padding: 2.5em 0 5em;
  overflow: hidden;
}

.blog-slider{
  @media screen and (min-width: 993px){
    .swiper{
      &-container{
        width: 100%;
      }
      &-wrapper{
        transform: unset !important;
        display: grid;
        grid-template-columns: repeat(3,calc(100% / 3 - 1.25em));
        gap: 1.875em;
      }
      &-slide{
        padding: 0;
        width: 100% !important;
      }
      &-nav{
        display: none;
      }
    }
  }
}

.about-index{
  padding: 5em 0 2.5em;
  &--pb40{
    padding-bottom: 2.5em;
  }
  &__inner{
    align-items: center;
  }
  .text{
    margin-top: 1.875em;
  }
  &__nav{
    display: flex;
    margin-top: 1.875em;
  }
  &__btn{
    padding: 0 1.75em;
    height: 2.5em;
    border-radius: .3125em;
    font-weight: 500;
    span{
      font-size: 1.0625em;
    }
  }
}

.main-slider,
.about-slider{
  .swiper{
    &-pagination{
      &-bullet{
        background-color: rgba(#fff,.5);
        &:hover,
        &-active{
          background-color: #fff;
        }
      }
    }
  }
}

.about-slider{
  overflow: hidden;
  .swiper{
    &-container{
      width: 100%;
    }
    &-slide{
      width: 100%;
      padding: 0;
    }
    &-pagination{
      bottom: 1.25em;
    }
    &-button{
      width: 1.5em;
      height: 1.5em;
      position: absolute;
      top: 50%;
      transform: translateY(-50%);
      z-index: 2;
      &:hover{
        path{
          stroke: $darkBlue;
        }
      }
      &-prev{
        left: 1.25em;
      }
      &-next{
        right: 1.25em;
      }
    }
  }
  &__img{
    @include imgCover(64.7%);
    border-radius: .0625em;
  }
}

.about-index2{
  padding: 2.5em 0 5em;
}

.blog-index2{
  padding: 5em 0 2.5em;
  overflow: hidden;
}

.blog-slider2{
  .swiper{
    &-slide{
      width: 50%;
      opacity: 0;
      pointer-events: none;
      transition: all .3s;
      &-active,
      &-next{
        opacity: 1;
        pointer-events: unset;
      }
    }
  }
  .blog-card{
    &__img{
      padding-bottom: 47.6%;
    }
  }
}

.main-slider{
  position: relative;
  overflow: hidden;
  &__img{
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    img{
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
    ._view2{
      display: none;
    }
  }
  .swiper{
    &-wrapper{
      align-items: stretch;
    }
    &-slide{
      height: auto;
      min-height: 50em;
      position: relative;
    }
    &-pagination{
      bottom: 1.5625em;
    }
  }
  &__inner{
    padding: 14em 0;
    position: relative;
    z-index: 2;
    @include grid100gap(.75em);
  }
  &__title{
    font-size: 4.5em;
    font-weight: 500;
  }
  &__desc{
    font-size: 2.375em;
    font-weight: 300;
  }
}


@media screen and (max-width: 992px){
  .blog-index{
    padding: 1.5625em 0 3.75em;
  }

  .blog-slider{
    .swiper-slide{
      width: 50%;
    }
  }

  .about-index{
    padding: 0 0 1.5625em;
    &__col2{
      order: -1;
    }
    .text{
      margin-top: .9375em;
      padding: 0 .625em;
    }
    &__nav{
      margin-top: 1.25em;
    }
  }

  .about-slider{
    width: calc(100% + 1.875em);
    margin-left: -.9375em;
    .swiper{
      &-pagination{
        bottom: .9375em;
      }
      &-button{
        &-prev{
          left: .625em;
        }
        &-next{
          right: .625em;
        }
      }
    }
  }

  .about-index2{
    padding: 1.5625em 0;
  }

  .main-slider{
    .swiper{
      &-slide{
        min-height: 28.125em;
      }
      &-pagination{
        bottom: .9375em;
      }
    }
    &__inner{
      padding: 6.25em 0;
      gap: .25em;
    }
    &__title{
      font-size: 2.875em;
    }
    &__desc{
      font-size: 1.25em;
    }
  }
}


@media screen and (max-width: 576px){
  .blog-slider2,
  .blog-slider{
    .swiper-slide{
      width: 100%;
    }
  }

  .about-index{
    &__btn{
      width: 100%;
      font-weight: 400;
      span{
        font-size: 1em;
      }
    }
  }

  .about-slider{
    &__img{
      padding-bottom: 100%;
      border-radius: 0;
    }
  }
}
