@import 'vars';
@import 'components/_text.scss';
@import 'components/_buttons.scss';
@import "components/_inputs.scss";
@import "components/grid.scss";
._scale-img{
  overflow: hidden;
  &:hover{
    img{
      transform: scale(1.05);
    }
  }
  img{
    transition: all .6s;
  }
}

.swiper-pagination{
  z-index: 1;
}

.fancybox__backdrop{
  background-color: #fff;
}

.carousel__button{
  background-color: rgba(24,24,27,0.92);
}
