.page-section{
  padding: 5em 0;
}

.series-card{
  display: flex;
  flex-direction: column;
  height: 100%;
  &__img{
    @include imgCover(100%);
  }
  &__content{
    padding: .9375em;
    flex-grow: 1;
  }
  &__desc{
    margin-top: .71em;
    @include truncate(3);
  }
}

.pagination{
  margin-top: 3.75em;
  @include center;
  &__item,
  &__btn{
    border-radius: .25em;
    border: 1px solid transparent;
    &:hover{
      color: $darkBlue;
      border-color: $darkBlue50;
    }
  }
  &__item{
    height: 1.7em;
    min-width: 1.6em;
    padding: 0 .125em;
    @include center;
    font-size: 1.25em;
    margin: 0 .375em;
    &._active{
      pointer-events: none;
      font-weight: 500;
      border-color: $darkBlue;
    }

  }
  &__nav{
    display: flex;
    margin: 0 .9375em;
    &:first-child{
      margin-left: 0;
    }
    &:last-child{
      margin-right: 0;
    }
  }
  &__btn{
    width: 1.625em;
    height: 1.7em;
    @include center;
    @include lastM(0 .9375em 0 0);
    svg{
      width: 100%;
    }
    &._disabled{
      opacity: .5;
    }
  }
}


@media screen and (max-width: 992px){
  .page-section{
    padding: 1.875em 0 3.75em;
  }

  .series-card{
    &__content{
      padding: .625em;
    }
    &__desc{
      margin-top: .355em;
    }
  }
}


@media screen and (max-width: 576px){
  .series-card{
    &__desc{
      margin-top: .42em;
    }
  }

  .pagination{
    &__item{
      font-size: 1em;
      min-width: 1.4375em;
      height: 1.5625em;
      line-height: 1.5625em;
      margin: 0 .15625em;
    }
    &__btn{
      @include lastM(0);
      height: 1.5625em;
    }
  }
}
