.blog{
  padding: 3.125em 0 0;
  &--pb80{
    padding-bottom: 5em;
  }
  &__grid{
    margin-top: 1.875em;
  }
  &__top{
    @include grid100gap(.9375em);
  }
  &__desc{
    color: $darkBlue50;
  }
  &__img{
    margin-top: 1.875em;
    img{
      width: 100%;
      display: block;
      border-radius: .0625em;
    }
    ._view2{
      display: none;
    }
  }
  .article{
    margin: 2.5em auto 0;
    width: 79.3125em;
    max-width: 100%;
  }
}

.blog-card{
  display: block;
  &__img{
    @include imgCover(73.4%);
    border-radius: .0625em;
  }
  &__content{
    padding: .94em;
    @include grid100gap(.625em)
  }
  &__title,
  &__desc{
    @include truncate(2);
  }
  &__title{
    min-height: 2.4em;
  }
}

.article-content{
  font-size: 1.125em;
  line-height: 130%;
  font-weight: 300;
  *{
    line-height: inherit;
  }
  p{
    @include lastM(0 0 1em);
  }
  h3{
    font-size: 1.11em;
    font-weight: 700;
    line-height: 130%;
    @include lastM(0 0 .9em);
  }
  ul{
    padding-left: .5em;
    @include grid100gap(.25em);
    li{
      padding-left: 1em;
      position: relative;
      &:before{
        content: "";
        position: absolute;
        top: 0;
        left: 0;
      }
    }
  }
  b{
    font-weight: 500;
  }
}

.subscribe{
  background-color: $darkBlue;
  color: $lightBlue;
  padding: 5.69em 0;
  &._reverse{
    @media screen and (min-width: 993px){
      .subscribe__inner{
        width: 100%;
      }
      .subscribe__col2{
        order: -1;
      }
    }
  }
  &__inner{
    margin: 0 auto;
    width: 79.3125em;
    max-width: 100%;
    align-items: center;
  }
  &__title{
    font-weight: 400;
  }
  &__logo{
    width: 14.25em;
    margin: 0 auto;
    display: block;
  }
}

.subscribe-form{
  margin-top: 1.25em;
  width: 31.625em;
  max-width: 100%;
  &__grid{
    display: flex;
  }
  &__btn{
    flex-shrink: 0;
    border-radius: 0 .3125em .3125em 0;
    padding: 0 1.875em;
    height: 2.875em;
    span{
      font-size: 1.125em;
    }
  }
  .fg{
    flex-grow: 1;
    input{
      font-size: 1em;
      padding: 0 1.25em;
      height: 2.875em;
      border-radius: 0.3125em 0 0 0.3125em;
      border-color: $lightBlue;
      color: $lightBlue;
      @media screen and (min-width: 993px){
        border-right: none;
      }
      &::placeholder{
        color: rgba($lightBlue, 0.60);
      }
    }
  }
  .m-check{
    margin-top: .625em;
    input {
      &:checked+label {
        &::before {
          background-color: $lightBlue;
        }
      }
    }
    label{
      &:before{
        border-color: $lightBlue;
        background-image: url(../img/check2.svg);
      }
    }
  }
}


@media screen and (max-width: 992px){
  .blog{
    padding: 0;
    margin-top: -.625em;
    &--pb80{
      padding-bottom: 2.5em;
    }
    &__grid{
      margin-top: 1.5625em;
    }
    &__top{
      gap: .625em;
    }
    &__img{
      margin-top: 1.5625em;
    }

    .article{
      margin-top: 1.25em;
      padding: 0 .625em;
    }
  }

  .article-content{
    font-size: .875em;
  }

  .subscribe{
    padding: 3.125em 0;
    &__inner{
      display: block;
    }
    &__logo,
    &__col2{
      display: none;
    }
  }
}


@media screen and (max-width: 576px){
  .blog{
    &__desc{
      font-size: 1em;
    }
    &__img{
      ._view1{
        display: none;
      }
      ._view2{
        display: block;
      }
    }
  }

  .blog-card{
    &__content{
      padding: .9375em .625em 0;
    }
    &__title{
      font-size: 1em;
    }
  }

  .subscribe-form{
    margin-top: 1.5625em;
    position: relative;
    padding-bottom: 4em;
    .fg{
      input{
        font-size: .875em;
        border-radius: .36em;
        padding: 0 1.43em;
        height: 3.14em;
      }
    }
    &__btn{
      position: absolute;
      left: 0;
      bottom: 0;
      width: 100%;
      height: 2.5em;
      border-radius: .3125em;
      span{
        font-size: 1em;
      }
    }
    .m-check{
      label{
        padding-left: 1.25em;
        padding-top: .125em;
      }
      &__title{
        font-size: .625em;
      }
    }
  }
}
