.collection{
  padding: 5em 0;
  overflow: hidden;
  &--new{
    background-color: $lightBlue;
  }
}

.slider{
  position: relative;
  &__top{
    margin-bottom: 1.875em;
  }
  .swiper{
    &-container{
      width: calc(100% + 1.875em);
    }
    &-slide{
      padding-right: 1.875em;
    }
    &-nav{
      position: absolute;
      bottom: 100%;
      right: 0;
      display: flex;
      align-items: center;
      margin-bottom: 1.875em;
    }
    &-button{
      position: static;
      width: 1.875em;
      height: 1.875em;
      @include center;
      margin: 0 0 0 .625em;
      svg{
        width: 100%;
      }
      &:hover{
        ._fill{
          fill: $darkBlue;
        }
        ._stroke{
          stroke: #fff;
        }
      }
    }
  }
}

.collection-slider{
  .swiper{
    &-slide{
      width: 25%;
      opacity: 0;
      pointer-events: none;
      transition: all .3s;
      &-active,
      &-next{
        opacity: 1;
        pointer-events: unset;
      }
      @media screen and (min-width: 577px){
        &-next+.swiper-slide{
          opacity: 1;
          pointer-events: unset;
        }
      }
      @media screen and (min-width: 993px){
        &-next+.swiper-slide+.swiper-slide{
          opacity: 1;
          pointer-events: unset;
        }
      }
    }
  }
}

.comments{
  padding: 5em 0;
  &__inner{
    width: 63.125em;
    max-width: 100%;
    margin: 0 auto;
  }
  &__top{
    margin-bottom: 1.875em;
  }
  &__items{
    margin-top: 1.875em;
    @include grid100gap(2em);
  }
}

.comments-auth{
  border-radius: 0.3125em;
  background-color: $lightGrey;
  padding: 1.25em;
  font-weight: 300;
  &__btn{
    font-weight: 500;
    text-decoration: underline;
    color: inherit;
    &:hover{
      text-decoration: none;
    }
  }
}

.comments-form{
  textarea{
    font-size: 1em;
    padding: .94em;
    border-radius: 0.3125em;
  }
  &__nav{
    display: flex;
    justify-content: flex-end;
    margin-top: .75em;
  }
  &__btn{
    padding: 0 1.625em;
    height: 2.6875em;
    border-radius: 0.3125em;
    span{
      font-size: 1.125em;
    }
  }
}

.comment-group{
  &__bottom{
    padding: 1.875em 0 0 1.875em;
  }
}

.comment-card{
  display: grid;
  grid-template-columns: 3.75em calc(100% - 3.75em - .9375em);
  gap: .9375em;
  &__top{
    display: flex;
    align-items: center;
  }
  &__date{
    margin-left: 1em;
    color: rgba($darkBlue,.4);
  }
  &__text{
    margin-top: .25em;
    font-weight: 300;
    color: rgba($darkBlue,.7);
  }
  &__nav{
    display: flex;
    margin-top: .25em;
  }
  &__btn{
    font-size: .875em;
    font-weight: 500;
    text-decoration-line: underline;
    color: $darkBlue;
    &:hover{
      text-decoration-color: transparent;
    }
  }
}

.avatar{
  @include imgCover(100%);
  border-radius: 50%;
}


@media screen and (max-width: 992px){
  .collection{
    padding: 2.5em 0;
    &._hide-tablet{
      display: none;
    }
    &--new{
      padding: 1.875em 0 3.125em;
    }
  }

  .slider{
    &__top{
      margin-bottom: 1.25em;
    }
    .swiper{
      &-container{
        width: calc(100% + 1.25em);
      }
      &-slide{
        padding-right: 1.25em;
      }
      &-nav{
        margin-bottom: 1.25em;
      }
    }
  }

  .collection-slider{
    .swiper{
      &-slide{
        width: calc(100% / 3);
      }
    }
  }

  .comments{
    padding: 2.5em 0 3.125em;
    &__top{
      margin-bottom: .9375em;
    }
    &__items{
      margin-top: 2.5em;
    }
  }

  .comments-auth{
    padding: 1.125em;
  }
}


@media screen and (max-width: 576px){
  .slider{
    .swiper{
      &-button{
        width: 1.25em;
        height: 1.25em;
      }
    }
  }

  .collection-slider{
    .swiper{
      &-slide{
        width: 50%;
      }
    }
  }

  .comments-form{
    textarea{
      font-size: .875em;
      padding: 1.07em;
      border-radius: 0.357em;
      height: 4.93em;
    }
    &__btn{
      height: 2.5em;
      width: 100%;
      span{
        font-size: 1em;
      }
    }
  }

  .comment-card{
    &__btn{
      font-size: 1em;
    }
  }
}
