.catalog-item{
  padding-top: 3.125em;
  &__grid{
    display: grid;
    grid-template-columns: 53.75em calc(100% - 53.75em - 2.375em);
    gap: 2.375em;
  }
}

.swiper-pagination{
  @include center;
  width: 100%;
  &-bullet{
    margin: 0 .3125em !important;
    width: 0.5em;
    height: 0.5em;
    background-color: $darkBlue50;
    opacity: 1;
    &:hover{
      background-color: $darkBlue;
    }
    &-active{
      background-color: $darkBlue;
      width: 0.9375em;
      height: 0.9375em;
    }
  }
}

.gallery{
  position: sticky;
  top: 1em;
  display: grid;
  grid-template-columns: 14.375em calc(100% - 14.375em - 1.875em);
  gap: 1.875em;
  .swiper{
    height: 50em;
    max-height: calc(100 * var(--vh) - 4em);
    &-pagination{
      bottom: .625em;
      right: 0;
      top: unset;
      transform: unset;

    }
  }
  &__img{
    @include imgCover(100%);
    padding-bottom: 0;
    height: 100%;
  }
  &__small{
    position: relative;
    padding: 1.875em 0;
    .gallery__img{
      border-radius: 0.625em;
      border: 1px solid transparent;
      cursor: pointer;
      transition: all .3s;
      &:hover{
        border-color: $darkBlue;
      }
    }
    .swiper{
      &-slide{
        height: 20em;
        padding-bottom: .9375em;
        &-thumb-active{
          .gallery__img{
            border-color: rgba($darkBlue, 0.20);
          }
        }
      }
      &-button{
        transition: all .3s;
        margin: 0;
        left: -1px;
        right: unset;
        height: 1.875em;
        width: calc(100% + 2px);
        background-color: $lightGrey;
        &:hover{
          background-color: $darkBlue;
          path{
            stroke: #fff;
          }
        }
        &-prev{
          top: 0;
          border-radius: .3125em .3125em 0 0;
        }
        &-next{
          top: unset;
          bottom: 0;
          border-radius: 0 0 .3125em .3125em;
        }
      }
    }
  }
  &__big{
    position: relative;
    .swiper{
      &-button{
        width: 1.5em;
        height: 1.5em;
        @include center;
        svg{
          width: 100%;
        }
        &-prev{
          left: 1.25em;
        }
        &-next{
          right: 1.25em;
        }
      }
    }
  }
  &__badges{
    position: absolute;
    right: .9375em;
    top: 0;
    z-index: 3;
    .badge{
      width: 4.375em;
      height: 4.375em;
      margin-bottom: .9375em;
      &__title{
        font-size: 1.25em;
      }
    }
  }
}

.catalog-info{
  &__top{
    @include grid100gap(.625em);
    margin-bottom: 1.25em;
  }
  &__desc{
    color: rgba($darkBlue,.2);
  }
}

.shops{
  &__top{
    padding: .3125em 0;
  }
  &__items{
    padding: .625em 0;
    display: flex;
    width: calc(100% + .625em);
  }
  &__item{
    height: 2.6875em;
    padding: .625em 1.875em;
    border-radius: 0.3125em;
    margin-right: 1.25em;
    @include center;
    &:not(:hover){
      background: $darkBlue;
      path{
        fill: #fff;
      }
      ._transparent{
        fill: $darkBlue;
      }
    }
    svg{
      max-width: 100%;
      max-height: 100%;
    }
    &--yandex{
      background: #FED42B;
    }
    &--wildberries{
      background: linear-gradient(90deg, #C02180 0%, #4E327C 100%);
    }
    &--ozon{
      background: #0069FF;
    }
  }
}

.info-card{
  margin-top: 1.25em;
  &__top{
    padding: .3125em 0;
  }
  &__bottom{
    padding: .625em 0;
    @media screen and (min-width: 993px){
      display: block !important;
    }
  }
}

.prop-item{
  padding: .5em 0;
  display: flex;
  justify-content: space-between;
  font-weight: 300;
  position: relative;
  &:before{
    content: '';
    position: absolute;
    bottom: .625em;
    height: 1px;
    width: 100%;
    background-color: rgba($darkBlue,.2);
  }
  &__title,
  &__desc{
    background-color: #fff;
    position: relative;
    z-index: 2;
    font-size: 1.125em;
  }
  &__title{
    padding-right: .55em;
  }
  &__desc{
    padding-left: .55em;
  }
}


@media screen and (max-width: 992px){
  .catalog-item{
    padding-top: 0;
    overflow: hidden;
    &__grid{
      grid-template-columns: calc(100% - 22.5em - .9375em) 22.5em;
      gap: .9375em;
    }
  }

  .swiper-pagination{
    &-bullet{
      width: .375em;
      height: .375em;
      &-active{
        width: .5625em;
        height: .5625em;
      }
    }
  }

  .gallery{
    grid-template-columns: 100%;
    gap: 0;
    .swiper{
      height: auto;
      max-height: unset;
      &-pagination{
        bottom: 1.5625em;
      }
    }
    &__img{
      padding-bottom: 133%;
      height: auto;
    }
    &__small{
      display: none;
    }
    &__big{
      position: relative;
      .swiper{
        &-button{
          width: 1.25em;
          height: 1.25em;
          &-prev{
            left: .25em;
          }
          &-next{
            right: .25em;
          }
        }
      }
    }
    &__badges{
      top: .9375em;
      .badge{
        font-size: 1em;
        width: 3.125em;
        height: 3.125em;
        line-height: 3.125em;
        margin-bottom: .625em;
        &__title{
          font-size: .9375em;
        }
      }
    }
  }

  .catalog-info{
    &__title{
      font-size: 1.25em;
    }
    &__desc{
      font-size: 1em;
    }
  }

  .shops{
    padding: 0 .625em;
    &__items{
      margin-top: -.625em;
      flex-wrap: wrap;
    }
    &__item{
      margin: .625em .625em 0 0;
      height: 2.5em;
      padding: .625em 1.75em;
      &--yandex{
        order: 2;
      }
      &--ozon{
        order: -1;
      }
    }
  }

  .info-card{
    margin-top: .9375em;
    padding-bottom: .625em;
    border-bottom: 1px solid rgba($darkBlue,.2);
    &._active{
      .info-card__top:after{
        transform: rotate(-180deg);
      }
    }
    &__top{
      padding: .3125em .625em;
      display: flex;
      align-items: center;
      justify-content: space-between;
      cursor: pointer;
      &:after{
        content: '';
        flex-shrink: 0;
        width: 1.25em;
        height: 1.25em;
        background-image: url(../img/arrow-bottom.svg);
        @include bgImgSize(100%);
        margin-left: 1em;
        transition: all .3s;
      }
    }
    &__bottom{
      display: none;
      padding: .625em;
    }
  }
}


@media screen and (max-width: 576px){
  .catalog-item{
    &__grid{
      grid-template-columns: 100%;
    }
  }

  .shops{
    &__items{
      width: calc(100% + 1em);
    }
  }
}
