.page-top{
    padding: 1.875em 0;
    background-color: $lightGrey;
    position: relative;
    .container{
        position: relative;
        z-index: 2;
    }
    &__title{
        padding: 5.125em 0;
        margin-top: 1.25em;
    }
    &__img{
        position: absolute;
        top: 0;
        right: 0;
        width: 100%;
        height: 100%;
        display: flex;
        justify-content: flex-end;
        img{
            display: block;
            height: 100%;
            width:  auto;
        }
        ._view2{
            display: none;
        }
    }
    &__nav{
        display: flex;
        margin-top: 1.875em;
    }
    &__back{
        display: flex;
        align-items: center;
        font-size: 1.25em;
        svg{
            transform: translateY(-5%);
            width: 1.25em;
            margin-right: .5em;
        }
    }
    &._small{
        .page-top__title{
            padding: 2.3125em 0;
        }
    }
    &._white-bg{
        background-color: transparent;
    }
}


.breadcrumbs {
    position: relative;
    z-index: 2;
    &__item {
        position: relative;
        display: inline;
        color: rgba($darkBlue,.5);
        font-weight: 300;
        font-size: 1em;
        line-height: 100%;
        &:not(:first-child) {
            margin-left: .5em;
            &:before {
                content: '/';
                position: absolute;
                z-index: 1;
                top: .125em;
                right: 100%;
                margin-right: .1875em;
            }
        }
        &:last-child{
            font-weight: 400;
            pointer-events: none;
        }
        &:hover{
            color: $darkBlue;
        }
    }
}



@media screen and (max-width: 992px){
    .page-top{
        padding: 1.5625em 0;
        &__title{
            padding: 3em 0 3em .9375em;
        }
        &._small{
            .page-top__title{
                padding: 2.375em 0 2.375em .9375em;
            }
        }
    }
}



@media screen and (max-width: 576px){
    .page-top{
        &__img{
            ._view1{
                display: none;
            }
            ._view2{
                display: block;
            }
        }
        &__nav{
            margin-top: 1.25em;
        }
        &__back{
            font-size: .875em;
            svg{
                margin-right: 0;
            }
        }
    }

    .breadcrumbs{
        &__item{
            font-size: .875em;
        }
    }
}
