.header{
  border-bottom: 1px solid rgba($darkBlue, 0.60);
  background-color: $lightGrey;
  padding: 1.875em 0;
  position: relative;
  &._white-bg{
    background-color: #fff;
    .catalog-menu{
      background-color: #fff;
    }
  }
  &__inner{
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding-right: 15.5625em;
    position: relative;
  }
  &__logo{
    display: block;
    width: 9.375em;
    flex-shrink: 0;
    img{
      width: 100%;
      display: block;
    }
    ._view2{
      display: none;
    }
  }
  &._show-catalog{
    .catalog-menu{
      opacity: 1;
      pointer-events: unset;
    }
    .catalog-btn{
      svg{
        transform: rotate(-180deg) translateY(-20%);
      }
    }
  }
}

.burger{
  display: none;
}

.header-search,
.filter-search{
  position: relative;
  input{
    font-size: 1em;
    height: 2.5em;
    border-radius: 0.3125em;
    padding: 0 .94em 0 2.69em;
  }
  &__icon{
    position: absolute;
    left: .94em;
    top: 50%;
    transform: translateY(-50%);
    width: 1.125em;
  }
}

.header-search{
  width: 18.75em;
  flex-shrink: 0;
}

.menu{
  display: flex;
  align-items: center;
}

.nav{
  display: flex;
  align-items: center;
  &__link{
    @include lastM(0 1.5625em 0 0);
    font-size: 1em;
    border-top: 1px solid transparent;
    border-bottom: 1px solid transparent;
    &:hover{
      color: $darkBlue;
      border-bottom-color: $darkBlue;
    }
  }
}

.catalog-btn{
  display: flex;
  align-items: center;
  font-weight: 700;
  height: 2.5em;
  padding: 0 1em;
  margin-right: 1.5625em;
  &:hover{
    .catalog-btn__title{
      border-bottom-color: $darkBlue;
    }
  }
  svg{
    width: .625em;
    margin-left: .3125em;
  }
  &__title{
    transition: all .3s;
    font-size: 1em;
    border-top: 1px solid transparent;
    border-bottom: 1px solid transparent;
  }
}

.header-auth{
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  right: 0;
  display: flex;
  &__link{
    padding: 0 1.25em;
    height: 2.5em;
    border-radius: .3125em;
    @include center;
    font-size: 1em;
    font-weight: 500;
    margin-left: 1.25em;
    &:not(.m-btn-dark){

    }
  }
  .m-btn-dark{
    padding: 0 1em;
  }
}

.header-user{
  width: 12.8125em;
  white-space: nowrap;
  display: grid;
  grid-template-columns: 2.5em calc(100% - 3.125em);
  gap: .625em;
  align-items: center;
  &__title{
    font-size: 1em;
    line-height: 120%;
    font-weight: 700;
  }
}

.catalog-menu{
  opacity: 0;
  pointer-events: none;
  transition: all .3s;
  position: absolute;
  top: 100%;
  padding: 1.875em 0 5em;
  width: 100%;
  background-color: $lightGrey;
  z-index: 10;
  margin-top: 1px;
  min-height: calc(100vh - 6.25em);
  &__top{
    display: none;
  }
  &__inner{
    display: grid;
    grid-template-columns: 12.5em calc(100% - 12.5em - 5.31em);
    justify-content: space-between;
  }
  &__col1{
    padding-top: 1.56em;
  }
  &__category{
    display: flex;
    align-items: center;
    justify-content: space-between;
    height: 2.4375em;
    padding: 0 .75em 0 1.75em;
    border-radius: 0.3125em;

    width: 100%;
    span{
      font-size: 1.125em;
    }
    svg{
      width: .9375em;
    }
    @media screen and (min-width: 993px){
      &:hover,
      &._active{
        background-color: $darkBlue;
        color: #fff;
        path{
          stroke: #fff;
        }
      }
      &._active{
        font-weight: 500;
      }
    }
  }
}

.submenu{
  display: none;
  padding-top: 1.875em;
  &._active{
    display: block;
  }
  &__top{
    @media screen and (min-width: 992px){
      display: block;
      .catalog-menu__back{
        display: none;
      }
      .text20{
        font-size: 1.875em;
      }
    }
  }
  &__grid{
    column-count: 4;
    column-gap: 3.125em;
    max-width: 80em;
    margin-top: 1.875em;
    &--series{
      column-count: 3;
    }
  }
  &__group{
    display: inline-block;
    width: 100%;
    @include lastM(0 0 1.875em);
  }
  &__title{
    font-size: 1.25em;
    font-weight: 500;
  }
  &__series-item{
    display: inline-block;
    width: 100%;
    font-size: 1.25em;
    @include lastM(0 0 .75em);
    &:hover{
      color: $darkBlue;
      font-weight: 500;
    }
  }
}

.submenu2{
  margin-top: 1.25em;
  &__links{
    display: flex;
    flex-direction: column;
    align-items: flex-start;
  }
  &__link{
    font-size: 1em;
    font-weight: 300;
    @include lastM(0 0 .625em);
    &:hover{
      font-weight: 400;
    }
  }
}


@media screen and (max-width: 1920px){
  .nav{
    &__link{
      @include lastM(0 1em 0 0);
    }
  }

  .catalog-btn{
    margin-right: 1em;
    padding: 0 .5em;
  }
}


@media screen and (max-width: 1730px){
  .header{
    &__inner{
      font-size: .9375em;
    }
    &__logo{
      width: 8.5em;
    }
  }

  .submenu{
    &__grid{
      column-count: 3;
    }
  }
}


@media screen and (max-width: 992px){
  .header{
    padding: 1em 0;
    &:before{
      content: '';
      position: absolute;
      background-color: inherit;
      width: 100%;
      height: 100%;
      z-index: 14;
      top: 0;
      left: 0;
    }
    &__inner{
      padding: 0;
      font-size: 1em;
    }
    &__logo{
      position: relative;
      z-index: 15;
    }
    &._open-menu{
      .menu{
        opacity: 1;
        pointer-events: unset;
      }
      .burger{
        ._view1{
          opacity: 0;
        }
        ._view2{
          opacity: 1;
        }
      }
    }
    &._show-catalog{
      .catalog-menu__wrap{
        transform: translateX(0);
      }
    }
    &._white-bg{
      .catalog-menu__wrap,
      .menu,
      .submenu,
      .submenu2{
        background-color: #fff;
      }
    }
  }

  .header-search{
    width: auto;
    margin: 0 .9375em;
    flex-grow: 1;
    z-index: 15;
  }

  .burger{
    width: 1.875em;
    height: 1.875em;
    position: relative;
    display: flex;
    z-index: 15;
    img{
      @include centerAbsolute;
      width: 100%;
      display: block;
      transition: all .3s;
    }
    ._view2{
      opacity: 0;
    }
  }

  .menu{
    position: fixed;
    background-color: $lightGrey;
    top: 0;
    left: 0;
    width: 100%;
    height: $vh;
    padding: 5.125em .9375em .9375em;
    overflow-y: auto;
    flex-direction: column;
    align-items: flex-start;
    z-index: 10;
  }

  .submenu__series-item,
  .submenu__title,
  .submenu2__link,
  .nav__link{
    min-height: 2.875em;
    padding: 0 .9375em;
    margin: 0 !important;
    display: flex;
    align-items: center;
    font-weight: 400;
  }

  .nav{
    flex-direction: column;
    align-items: flex-start;
  }

  .catalog-btn{
    min-height: 3em;
    padding: 0 .9375em;
    font-weight: 500;
    width: 100%;
    margin: 0;
    background-image: url(../img/arrow-right.svg);
    background-repeat: no-repeat;
    background-position: right .9375em center;
    background-size: .5em auto;
    svg{
      display: none;
    }
  }

  .header-auth{
    position: static;
    flex-direction: column;
    align-items: flex-start;
    transform: unset;
    &__link{
      background-color: transparent;
      height: unset;
      min-height: 2.875em;
      padding: 0 .9375em;
      margin: 0 !important;
      display: flex;
      align-items: center;
      font-weight: 400;
    }
    .m-btn-dark{
      background-color: transparent;
      color: $darkBlue;
      border: none;
    }
  }

  .header-user{
    margin: 1em .9375em 0;
  }

  .submenu2,
  .submenu,
  .catalog-menu{
    position: fixed;
    overflow-y: auto;
    top: 0;
    left: 0;
    margin: 0;
    height: $vh;
    padding: 4.25em 0 .9375em;
    background-color: $lightGrey;
    width: 100%;
  }

  .submenu,
  .submenu2{
    transform: translateX(100%);
    pointer-events: none;
    transition: all .3s;
  }
  .menu{
    opacity: 0;
    pointer-events: none;
    transition: all .3s;
  }

  .catalog-menu{
    z-index: 11;
    min-height: unset;
    overflow: hidden;
    padding: 0;
    opacity: 1;
    background-color: transparent !important;
    &__wrap{
      height: 100%;
      overflow-y: auto;
      padding: 4.25em 0 .9375em;
      transform: translateX(100%);
      transition: all .3s;
      background-color: $lightGrey;
    }
    .container{
      padding: 0;
    }
    &__inner{
      display: block;
    }
    &__col1{
      padding: .9375em;
    }
    &__category{
      height: 2.75em;
      padding: 0 .9375em;
      span{
        font-size: 1em;
      }
    }
    &__top{
      @include center;
      padding: 0 .9375em;
      height: 4em;
      position: relative;
    }
    &__back{
      position: absolute;
      left: .9375em;
      padding: .9375em;
      img{
        width: .5em;
      }
    }
  }

  .submenu{
    z-index: 12;
    &._show{
      opacity: 1;
      pointer-events: unset;
      transform: translateX(0);
    }
    &__grid{
      margin: 0;
      padding: .9375em;
      column-count: 1;
    }
    &__title{
      font-weight: 500;
      font-size: 1em;
      justify-content: space-between;
      &:after{
        content: '';
        background-image: url(../img/arrow-right.svg);
        @include bgImgSize(100%);
        flex-shrink: 0;
        width: .5em;
        height: 2em;
      }
    }
    &__group{
      margin-bottom: 0 !important;
      &._show{
        .submenu2{
          opacity: 1;
          pointer-events: unset;
          transform: translateX(0);
        }
      }
    }
    &__series-item{
      font-size: 1em;
    }
  }

  .submenu2{
    z-index: 13;
    &__links{
      padding: .9375em;
    }
  }
}


@media screen and (max-width: 576px){
  .header{
    &__logo{
      width: 3.125em;
      ._view1{
        display: none;
      }
      ._view2{
        display: block;
      }
    }
  }
}
